<template lang="html">
  
  <section class="section4">
    <h2 class="section4-title">
      section4
      <i class="mui-icon mui-icon-arrowright"></i>
    </h2>
    <div class="section4-list">
      <router-link to="/detail" class="section4-item" v-for="item in list" :key="list.id" tag="div">
        <img :src="item.imgPath" alt="">
        <h1 class="title">{{item.intro}}</h1>
        <div class="info">
            <p class="price">
              <span class="now">￥{{ item.price }}</span>
              <span class="old">￥{{ item.price }}</span>
            </p>
            <p class="sell">
              <span>热卖中</span>
              <span>剩10件</span>
            </p>
        </div>
      </router-link>
    </div>
    <a class="section4-banner">
      <img v-lazy="banner">
    </a>
  </section>
</template>

<script>
export default {
  props: ['list', 'banner']
}
</script>

<style lang="less" scoped>
.section4 {
  width: 100%;
  overflow: hidden;
  padding-top: 6vw;
  .section4-title {
    text-align: center;
    padding: 4vw 0;
    position: relative;
    background-color: #ffffff;
    i {
      position: absolute;
      right: 6vw;
      top: 50%;
      transform: translateY(-50%);
      &::before {
        color: #9f9f9f;
      }
    }
  }
  .section4-list {
  display: flex;
  flex-wrap: wrap;
  padding: 7px;
  justify-content: space-between;

    .section4-item {
      width: 49%;
      border: 1px solid #ccc;
      box-shadow: 0 0 8px #ccc;
      margin: 4px 0;
      padding: 2px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      min-height: 293px;
      img {
        width: 100%;
      }
      .title {
        font-size: 12px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }

      .info {
        background-color: #eee;
        p {
          margin: 0;
          padding: 5px;
        }
        .price {
          .now {
            color: red;
            font-weight: bold;
            font-size: 16px;
          }
          .old {
            text-decoration: line-through;
            font-size: 12px;
            margin-left: 10px;
          }
        }
        .sell {
          display: flex;
          justify-content: space-between;
          font-size: 13px;
        }
      }
    }
  }
}
</style>



